.loader {
  width: 100px;
  height: 100px;
  border: 8px solid #3498db;
  border-top-color: #ff3d00;
  border-radius: 50%;
  margin: 0 auto;
  animation: spin 2s linear infinite;
  position: relative;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  color: #fff;
  background-color: #e74c3c;
  border: 1px solid #c0392b;
  padding: 10px;
  font-size: 16px;
  position: relative;
}

.error__icon {
  vertical-align: middle;
  margin-right: 5px;
  font-size: 24px;
}

.cat-info {
  width: 1500px;
  align-items: center;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.cat-info img {
  width: 1500px;
  height: auto;
  border-radius: 10px;
  display: block;
}

.cat-info h3 {
  color: rgba(255, 0, 98, .663);
  justify-content: center;
  margin-bottom: 5px;
  display: flex;
}

.cat-info p {
  color: rgba(255, 0, 34, .925);
}

